<template>
    <p-card style="width: 800px; margin: auto">
        <template #content>
            <div class="p-d-flex p-jc-between p-mb-3">
                <strong style="font-size: 1.5rem">Media Inventory</strong>
            </div>

            <p-data-table
                :value="mediaInventoryList"
                responsiveLayout="scroll"
                showGridlines
                class="p-datatable-sm p-mb-1"
            >
                <p-column header="Type" field="type" />
                <p-column header="Lot Number" field="lotNumber" />
                <p-column header="Start Date" field="startDate"/>
                <p-column header="End Date" field="endDate"/>
                <p-column style="width: 50px">
                    <template #body="{data}">
                        <p-button
                            icon="pi pi-pencil"
                            @click="showEditMediaInventoryDialog(data)"
                        />
                    </template>
                </p-column>
            </p-data-table>

            <div class="p-d-flex p-jc-between p-mt-2">
                <p-button
                    label="Add"
                    autofocus
                    @click="showAddMediaInventoryDialog"
                />
            </div>
        </template>
    </p-card>

    <p-dialog
        v-model:visible="addMediaInventoryDialog"
        :modal="true"
        style="width: 600px"
    >
        <template #header>
            <div class="table-header">
                <strong>{{ formIsEdit ? 'Edit Media' : 'Add Media' }}</strong>
            </div>
        </template>

        <div class="p-fluid">
            <div class="p-field">
                <label for="type">Type</label>
                <p-dropdown v-model="form.type" id="type" />
            </div>
            <div class="p-field">
                <label for="lotNumber">Lot Number</label>
                <p-input-text v-model="form.lotNumber" id="lotNumber" />
            </div>
            <div class="p-field">
                <label for="startDate">Start Date</label>
                <p-calendar dateFormat="mm/dd/y" v-model="form.startDate" id="startDate" :showIcon="true" />
            </div>
            <div class="p-field">
                <label for="endDate">End Date</label>
                <p-calendar dateFormat="mm/dd/y" v-model="form.endDate" id="endDate" :showIcon="true" />
            </div>
        </div>

        <div class="p-d-flex p-jc-between p-mt-2">
            <p-button
                label="Update"
                autofocus
                @click="edit"
                v-if="formIsEdit"
            />
            <p-button label="Save" autofocus @click="save" v-else />
        </div>
    </p-dialog>
</template>

<script>
import { reactive, ref } from "vue";

export default {
    setup() {
        const addMediaInventoryDialog = ref(false);
        const formIsEdit = ref(false);

        const form = reactive({
            id: null,
            type: null,
            lotNumber: null,
            startDate: null,
            endDate: null,
        });

        const mediaInventoryList = ref([
            { id: 1, type: 'Cycle', lotNumber: 'HD123', startDate: '2024-01-01', endDate: '2024-12-31' },
            { id: 2, type: 'Ivf', lotNumber: 'FD456', startDate: '2024-02-15', endDate: '2024-11-15' },
            { id: 3, type: 'Monitoring', lotNumber: 'SD789', startDate: '2024-03-10', endDate: '2024-09-30' },
        ]);

        const save = () => {
            if (formIsEdit.value) {
                edit();  // Call the edit method if we are editing an existing item
            } else {
                const newId = Math.max(...mediaInventoryList.value.map(item => item.id)) + 1;
                mediaInventoryList.value.push({ ...form, id: newId });
            }
            addMediaInventoryDialog.value = false;
        };

        const edit = () => {
            const index = mediaInventoryList.value.findIndex(item => item.id === form.id);
            if (index !== -1) {
                mediaInventoryList.value[index] = { ...form };
            }
            addMediaInventoryDialog.value = false;
        };

        const showAddMediaInventoryDialog = () => {
            formIsEdit.value = false;
            resetForm();
            addMediaInventoryDialog.value = true;
        };

        const showEditMediaInventoryDialog = (data) => {
            formIsEdit.value = true;
            Object.assign(form, data);
            addMediaInventoryDialog.value = true;
        };

        const resetForm = () => {
            form.id = null;
            form.type = null;
            form.lotNumber = null;
            form.startDate = null;
            form.endDate = null;
        };

        return {
            form,
            formIsEdit,
            addMediaInventoryDialog,
            mediaInventoryList,
            showAddMediaInventoryDialog,
            showEditMediaInventoryDialog,
            save,
            edit,
        };
    },
};
</script>

<style scoped>
.table-header {
    display: flex;
    align-items: ;
    justify-content: space-between;
}
</style>
